<template>
  <v-card class="mx-auto" max-width="344">
    <v-img src="https://www.uttt.edu.mx/img/about/logouttt.png" height="200px"></v-img>

    <v-card-title>
      Bienvenido a la UTTT "{{ getUserName }}"
    </v-card-title>

    <v-card-subtitle>
      Tu password es: {{ getPassword }}
    </v-card-subtitle>

    <v-card-actions class="justify-content-between">
      <v-btn outlined class="ma-1 pa-4" color="orange" v-on:click="redirectToLogin">
        Regresar
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'SaludoComponent',

  computed: {
    ...mapGetters(['getUserName', 'getPassword']),
  },

  created() {
    this.userName = this.getUserName;
    this.password = this.getPassword;
  },

  methods: {
    redirectToLogin() {
      this.$router.push("/");
    },
  },
}
</script>
